import React from "react"
import image from "../Static/image/em.PNG"
import card from "../Static/image/card.jpg"
export default function BirthdayCard() {
  return (
    <div class="birthday-card">
    <div class="cover-inner">
        <img src={image} width="100%" alt="text"/>
    </div>

    <div class="cover-outer">
        <img src={card} width="100%" alt="text"/>
    </div>

        <div class="text">
          <p class="title">
            Chc mng sinh nht <br/> Em
          </p>
          <p>
          Mng sinh nht em, anh ch chc em c nhiu sc khe thi. Cn v hnh phc v nim vui, anh mong rng chnh mnh s l ngi mang nhng iu  n vi em.
          </p>
          <p>Thng em!</p>
        </div>
    </div>
  )
};

